import React, { useEffect, useState } from 'react'
import axios from 'axios';
import {Line } from 'react-chartjs-2';
import { registerables, Chart } from "chart.js";
Chart.register(...registerables);

const StockMarketGraph = () => {

    const [data, setData] = useState([]);

    useEffect(()=>{
        const apiKey = `6KL9S67SND37A8P6`
        const symbol = 'AAPL'
        const apiUrl = `https://www.alphavantage.co/query?function=TIME_SERIES_INTRADAY&symbol=IBM&interval=5min&apikey=demo`
        console.log(apiKey);
      axios.get(apiUrl)
      .then(response =>{
        const dailyData = response.data['Time Series (5min)'];
        const dates = Object.keys(dailyData).reverse();
        const closingPrices = dates.map(date => dailyData[date]['4. close']);
        
       
        setData(closingPrices);
        console.log(data);
      })
      .catch(error =>{
        console.error("Error fetch data", error);
      })
    }, []);
  return ( 
    
  
    <div>

       <h4>IBM Stock prices </h4>
         <Line
        data={{
          labels: 'Closing price',
          datasets: [
            {
              label: 'Closing Price',
              data: data,
              borderColor: 'blue',
            },
          ],
        }}

      
      />
    </div>
  )
}

export default StockMarketGraph